export interface IProject {
  name: string;
  description: string;
  image: string;
  tech: string[];
  download?: string;
  repo?: string;
  preview?: string;
  website?: string;
  branch?: string;
  collaborator?: boolean;
  ciFile?: string;
  ci?: boolean;
  azure?: boolean;
}

export function getRepoWithBranch(project: IProject): string {
  if (project.branch)
    return project.repo + "/tree/" + project.branch;
  if (project.collaborator)
    return project.repo + "/commits?author=josetr";
  if (project.repo)
    return project.repo;
  return "";
}

