import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons'
import './App.scss';
import config, { getRepoWithBranch, IProject } from './Config';
import { Link } from 'react-scroll';

const { name, description, contact, projects } = config

function App() {
  return (
    <>
      <header>
        <nav>
          <ul className="nav-list top">
            <li><Link href="#work" to="work" duration={750} smooth>Work</Link></li>
            <li><Link href="#contact" to="contact" duration={750} smooth>Contact</Link></li>
            <li><a href="https://github.com/josetr" target="_blank" rel="noreferrer"><FontAwesomeIcon icon={faGithub} /></a></li>
          </ul>
        </nav>
        <div className="header-content">
          <h1>{name}</h1>
          <p>{description}</p>
          <a className="contact-btn" href={`mailto:${contact.email}`}>Contact me</a>
        </div>
      </header>
      <main>
        <section id="work">
          <div className="section-container">
            <div>
              <h2>My work</h2>
              <ul className="work-list">
                {projects.map(project =>
                  <li className="work" key={project.name}>

                    <div className="work-description">
                      <h3>{project.name}
                      </h3>
                      {project.repo && project.ci === true && <div>
                        {!project.azure && <a href={`${project.repo}/actions`}><img src={`${project.repo}/actions/workflows/${project.ciFile ?? "main.yaml"}/badge.svg`} alt="badge" /></a>}
                        {project.azure && <a href={`https://josetr.visualstudio.com/${project.name}/_build/latest?definitionId=18&branchName=master`}><img src={`https://josetr.visualstudio.com/${project.name}/_apis/build/status/${project.name}?branchName=master`} alt="badge" /></a>}
                      </div>
                      }
                      <p style={{ whiteSpace: "pre-line" }}>{project.description}</p>
                      <ul className="work-languages">{project.tech.map(tech =>
                        <li key={tech}>{tech}</li>
                      )}
                      </ul>
                      <div className="work-links">
                        {project.website &&
                          <a href={project.website} target="_blank" rel="noreferrer">View website</a>}
                        {project.preview &&
                          <a href={project.preview} target="_blank" rel="noreferrer">View Live Preview</a>}
                        {project.download &&
                          <a href={project.download} target="_blank" rel="noreferrer">Download</a>}
                        {project.repo && !project.website && !project.download &&
                          <a href={getRepoWithBranch(project)} target="_blank" rel="noreferrer">View repo</a>}
                        {project.repo &&
                          <a href={getRepoWithBranch(project)} target="_blank" rel="noreferrer" className="icon"> <FontAwesomeIcon icon={faGithub} size={"lg"} /></a>}
                      </div>
                    </div>
                    <div className="work-image">
                      {image(project)}
                    </div>
                  </li>
                )}
              </ul>
            </div>
          </div>
          <div className="container">
            <div id="about">
            </div>
          </div>
        </section>
        <section id="contact">
          <div className="section-container">
            <div className="contact">
              <h2>Contact me</h2>
              <p className="contact-description">{contact.description}</p>
              <a className="contact-btn" href={`mailto:${contact.email}`}>{contact.email}</a>
            </div>
          </div>
        </section>
      </main>
      <footer>
        <p>Copyright Â© 2021 {name}</p>
      </footer>
    </>
  );

  function image(project: IProject) {
    const href = project.website ?? project.preview ?? getRepoWithBranch(project);
    if (href)
      return <a href={href} target="_blank" rel="noreferrer"><img src={project.image} alt={project.name} /></a>

    return <a href={project.image}> <img src={project.image} alt={project.name} /></a>
  }
}

export default App;
